ul {
    display: flex;
    padding-left: 10px;
    justify-content: space-around;
  }
  
  /* Styling for the text for each method of contact. */
  
  .link-info {
    font-size: 23px;
    font-family: Arial, Helvetica, sans-serif;
  
    display: inline-block;
    font-weight: 550;
    margin-top: 30px;
  }
  
  /* Class of no-link-info was needed for pseudo-class of hover on the anchor tags. */
  
  .link-info a {
    border-radius: 0.25rem;
    padding: 5px;
    color: var(--white);
    background-color: var(--dark2);
    text-decoration: underline 4px var(--dark1);
    transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out,
      border-color 0.15s ease-in-out;
  }
  
  .link-info a:hover {
    background-color: var(--light1);
    text-decoration: underline 4px var(--dark2);
    color: var(--black);
  }