#header-nav {
    background-color: var(--dark3);
    border: solid 5px var(--dark2);
    padding: 20px;
    margin: 0;
    align-items: center;
  }
  
  #my-name {
    font-size: 60px;
    font-weight: 600;
    margin: 10px;
    padding: 10px;
    font-family: Arial, Helvetica, sans-serif;
    background-color: var(--dark1);
    color: var(--white);
    border-radius: 0.25rem;
  }
  
  #nav-links {
    font-size: 23px;
    font-family: Arial, Helvetica, sans-serif;
    margin: 6px;
    font-weight: 550;
    padding: 5px;
    color: var(--white);
    background-color: var(--dark1);
    text-decoration: underline 4px var(--dark2);
  }
  
  #nav-links:hover {
    background-color: var(--contrast1);
    text-decoration: underline 4px var(--dark1);
    color: var(--black);
  }
  .page-todo {
    font-size: 55px;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 570;
    background-color: var(--light3);
    color: var(--dark1);
    margin: 20px;
    padding: 10px;
    text-align: center;
  }